<template>
  <va-card :title="$t('tables.labelStocktransction')" class="w-100">
    <div class="row">
      <div class="flex md12 sm12">

        <va-data-table
          :fields="fields"
          :data="dataTable"
          :loading="loading"
          :totalPages="totalPages"
          @page-selected="readItems"
          api-mode
          hoverable
        >
          <template slot="marker" slot-scope="props">
            {{props.rowIndex + 1}}
          </template>
          <template slot="status" slot-scope="props">
            <va-badge v-if="props.rowData.is_Finish && props.rowData.is_Seen && (props.rowData.delivery !== null )" :color="'secondary'">
              {{ $t('tables.is_Shiped') }}
            </va-badge>
            <va-badge v-else-if="props.rowData.is_Finish && props.rowData.is_Seen" :color="'success'">
              {{ $t('tables.is_finshed') }}
            </va-badge>
            <va-badge v-else-if="props.rowData.is_Seen && props.rowData.is_Pending" :color="'info'">
              {{ $t('tables.is_Pending') }}
            </va-badge>
            <va-badge v-else-if="props.rowData.is_Seen" :color="'warning'">
              {{ $t('tables.is_SeenONly') }}
            </va-badge>

            <va-badge v-else-if="!props.rowData.is_Finish && !props.rowData.is_Seen" :color="'danger'">
              {{ $t('tables.is_SeenAndIsfinish') }}
            </va-badge>
          </template>
          <template slot="quantity" slot-scope="props">
            {{props.rowData.tr_Stock_Subs.length }}
          </template>
          <template slot="actions" slot-scope="props">
            <va-button flat small color="dark" :disabled="props.rowData.is_Finish && props.rowData.is_Seen && (props.rowData.delivery !== null )" @click="showEditModal(props.rowData)" class="ma-0">
              {{ $t('tables.loadingShipment') }}
            </va-button>

          </template>

        </va-data-table>
        <va-modal v-model="editModal" no-outside-dismiss
          size="large"
          :title="$t(modelTitle)"
          hide-default-actions >
          <va-data-table
            :fields="checkFildes"
            :data="tr_Stock_Subs"
            hoverable
          >
            <template slot="select" slot-scope="props">
              <va-checkbox :value="props.rowData.is_checked" @input="selectProduct(props.rowIndex)" />
            </template>
            <template slot="product" slot-scope="props">
              {{props.rowData.products.name }}
            </template>

            <template slot="units" slot-scope="props">
              {{props.rowData.units.name }}
            </template>
            <template slot="stock" slot-scope="props">
              {{props.rowData.stock.name }}
            </template>

          </va-data-table>
          <div class="row pt-3" style="justify-content: center;">
            <va-button color="success" @click="okEditfun()"> {{ $t('tables.finished') }}</va-button>
            <va-button color="info" @click="showEditModal(props.rowData)"> {{ $t('tables.pending') }}</va-button>
            <va-button color="danger" @click="cancleEditfun()"> {{ $t('tables.cancel') }}</va-button>

          </div>
        </va-modal>
        <va-modal v-model="deliveryModal" no-outside-dismiss
          size="large"
          :title="$t(modelTitle)"
          hide-default-actions >
          <div class="row">
            <form @submit.prevent="okdeliveryfun()">
              <div class="flex md12 sm12 xs12">
                <va-select
                  :label="$t('placeholder.delivery')"
                  v-model="delivery.value"
                  searchable
                  textBy="name"
                  keyBy="id"
                  :options="delivery.options"
                />
              </div>

              <div class="d-flex justify--center mt-3">
                <va-button type="button" color="gray" flat @click="cancledeliveryfun()">
                  {{ $t('modal.cancel') }}        </va-button>
                <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t('modal.ship') }}</va-button>
              </div>
            </form>

          </div>

        </va-modal>
      </div>

    </div>

  </va-card>

</template>

<script>
import StockTransactionService from '../../../../services/stock/StockTransactionService'
import EmployeesService from '../../../../services/BasicData/employeesService'

export default {
  data () {
    return {
      modelTitle: '',
      modelOktext: '',
      stopBtn: false,
      is_view: false,
      perPage: 10,
      totalPages: 0,
      dataTable: [],
      tr_Stock_Subs: [],
      editModal: false,
      deliveryModal: false,
      loading: true,
      is_seen: false,
      opjectBeforeEdit: {},
      delivery: {
        value: '',
        options: [],
      },
    }
  },
  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',

      }, {
        name: '__slot:status',
        title: this.$t('tables.headings.status'),
      }, {
        name: '__slot:quantity',
        title: this.$t('tables.headings.quantity'),
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
    checkFildes () {
      return [{
        name: '__slot:select',
        width: '30px',
        dataClass: 'text-center',
        title: '#',

      }, {
        name: '__slot:product',
        title: this.$t('tables.headings.product'),
      }, {
        name: 'quantity',
        title: this.$t('tables.headings.quantity'),
      }, {
        name: '__slot:units',
        title: this.$t('tables.headings.units'),
      }, {
        name: '__slot:stock',
        title: this.$t('tables.headings.stock'),
      }]
    },
  },
  created () {
    this.readItems()
  },
  methods: {

    selectProduct (index) {
      this.currentobject.tr_Stock_Subs[index].is_checked = !this.currentobject.tr_Stock_Subs[index].is_checked
    },
    /*  api Padination */

    readItems (page = 1) {
      this.loading = true
      console.log('fkghg')
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
        type: 1,
      }
      StockTransactionService.getAllPagnation(params)
        .then(response => {
          console.log(response)
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getdelivery () {
      EmployeesService.getAll()
        .then(response => {
          this.delivery.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    edit (user) {
      StockTransactionService.update(this.currentobject.id, this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.currentobject = {}
          this.loading = false
          this.stopBtn = false
          if (!this.is_seen) {
            this.editModal = false

            this.showToast(this.$t('generic.done'), {
              icon: 'fa-check',
              position: 'bottom-center',
              duration: 2500,
              type: 'success',
            })
          }
        } else {
          this.loading = false
          this.stopBtn = false
          this.editModal = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.stopBtn = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },

    /* edit model */
    showEditModal (cuobject) {
      this.tr_Stock_Subs = cuobject.tr_Stock_Subs
      this.editModal = true
      this.modelTitle = 'generic.editItemTitle'
      this.modelOktext = 'modal.edit'
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.currentobject = cuobject
      if (!this.currentobject.is_Seen) {
        this.is_seen = true
        this.currentobject.is_Seen = true
        // this.edit(this.currentobject)
      }
    },
    okEditfun () {
      this.stopBtn = true
      this.getdelivery()
      this.editModal = false
      this.currentobject.is_Finish = true
      this.edit(this.currentobject)
      this.deliveryModal = true
    },
    pendingEditfun () {
      this.currentobject.is_Pending = true
      this.edit(this.currentobject)
      this.editModal = false
    },
    cancleEditfun () {
      this.editModal = false
      this.loading = false
      this.stopBtn = false
      Object.assign(this.currentobject, this.opjectBeforeEdit)
    },

    okdeliveryfun () {
      this.stopBtn = true
      if (this.delivery.value !== '') {
        this.currentobject.fK_Delivery = this.delivery.value.id
      } else {
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
      this.edit(this.currentobject)
    },
    cancledeliveryfun () {
      this.deliveryModal = false
    },
    /* end edit model */
  },
}

</script>

<style lang="scss">
  i.fa.fa-exclamation {
    color: red !important;
  }

  .fitContentHight {
    height: fit-content;
  }
</style>
